/* ./src/App.css */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  background-color: #aee1f9;
  background-image: linear-gradient(315deg, #aee1f9 0%, #f6ebe6 74%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #1F2833; 
  color: #66FCF1; 
  padding: 10px 0; 
  width: 100%; 
  height: 40px;
}

.navbar-brand {
  font-size: 26px; 
  font-weight: 1.5rem;
}

.login{
  position: absolute;
  top: 10px;
  left: 20px;
  background-color:  #66FCF1; 
  color: #000000; 
  padding: 10px 30px; 
  border: solid #000000; 
  border-radius: 24px; 
  cursor: pointer !important; 
}

.detect-button {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #000000; 
  color: #FFFFFF; 
  padding: 15px 35px; 
  border: solid #66FCF1; 
  border-radius: 24px; 
  cursor: pointer !important; 
}

.upload{
  position: absolute;
  bottom: 20px;
  left: 58%;
  transform: translateX(-50%);
  background-color: #000000; 
  color: #FFFFFF; 
  padding: 15px 35px; 
  border: solid #66FCF1; 
  border-radius: 24px; 
  cursor: pointer !important; 
}

@media only screen and (max-width: 600px) {

  .login{
    position: absolute;
    top: 10px;
    left: 8px;
    background-color:  #66FCF1; 
    color: #000000; 
    padding: 8px 25px; 
    border: solid #000000;  
    cursor: pointer !important; 
  }
  
  .detect-button {
    /* Adjusted styles for phone view */
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
  }

  .upload {
    /* Adjusted styles for phone view */
    bottom: 50px;
    left: 70%;
    transform: translateX(-50%);
  }

  .detected-object {
    position: absolute;
    bottom: 350px; /* Adjusted position */
    left: 50%;
    transform: translateX(-50%);
  }
}



